import React, { Fragment, useEffect, useState } from 'react'
import axios from 'axios'
import {
    ChevronDown, Share, Printer, FileText, File, Grid, Copy, Plus, MoreVertical, Edit, Archive, Trash, Search, Eye, Lock, ChevronLeft
} from 'react-feather'
import {
    Card, CardHeader, CardTitle, Button, UncontrolledButtonDropdown, DropdownToggle, DropdownMenu, DropdownItem, Input, Label, Row, Col, Badge, Form, FormGroup, UncontrolledDropdown, Spinner, CardBody, TabContent, TabPane, Nav, NavItem, NavLink, InputGroup, InputGroupAddon, InputGroupText, CustomInput
} from 'reactstrap'
import useJwt from '@src/auth/jwt/useJwt'
import { Error, Success, ErrorMessage } from '../../../../../viewhelper'
import { Link, useHistory } from 'react-router-dom'
import Select from 'react-select'
import { selectThemeColors, transformInToFormObject } from '@utils'
import { divIcon } from 'leaflet'

const CreateQuota = () => {
    const [pointRuleloading, setPointRuleloading] = useState(false)
    const [userInput, setUserInput] = useState({
        facebook : 0,
        google : 0,
        whatsapp : 0,
        sms : 0,
        email : 0,
        push_notification : 0,
        instagram : 0
    })
    const handleChange = (e) => {
        setUserInput({ ...userInput, [e.target.name]: parseInt(e.target.value) })
    }
  
    const onSubmit = (e) => {
        e.preventDefault()
        const { facebook, google, whatsapp, sms, email, push_notification, instagram } = userInput
        console.log({ facebook, google, whatsapp, sms, email, push_notification, instagram })
        setPointRuleloading(true)
        useJwt.createQuotaList({ facebook, google, whatsapp, sms, email, push_notification, instagram }).then((response) => {
            setPointRuleloading(false)
            Success(response)
          }).catch((error) => {
            setPointRuleloading(false)
            Error(error)
            console.log(error)
          })
    }
    return (
        <Fragment>
            <Button.Ripple className='mb-1' color='primary' tag={Link} to='/allQuota' >
                    <div className='d-flex align-items-center'>
                            <ChevronLeft size={17} style={{marginRight:'5px'}}/>
                            <span >Back</span>
                    </div>
                    </Button.Ripple>
            <Card>
                <CardHeader className='border-bottom'>
                    <CardTitle tag='h4'>Create Quota</CardTitle>
                    
                </CardHeader>
                <CardBody style={{ paddingTop: '15px' }}>
                    <Form className="row" style={{ width: '100%' }} onSubmit={onSubmit} autoComplete="off">
                    <Col sm="6" >
                        <FormGroup>
                            <Label for="facebook">Facebook</Label>
                            <Input type="number"
                                name="facebook"
                                id='facebook'
                                value={userInput.facebook}
                                onChange={handleChange}
                                required
                                placeholder="0"
                            />
                        </FormGroup>
                    </Col>
                    <Col sm="6" >
                        <FormGroup>
                            <Label for="google">Google</Label>
                            <Input type="number"
                                name="google"
                                id='google'
                                value={userInput.google}
                                onChange={handleChange}
                                required
                                placeholder="0"
                            />
                        </FormGroup>
                    </Col>
                    <Col sm="6" >
                        <FormGroup>
                            <Label for="whatsapp">WhatsApp</Label>
                            <Input type="number"
                                name="whatsapp"
                                id='whatsapp'
                                value={userInput.whatsapp}
                                onChange={handleChange}
                                required
                                placeholder="0"
                            />
                        </FormGroup>
                    </Col>
                    <Col sm="6" >
                        <FormGroup>
                            <Label for="sms">SMS</Label>
                            <Input type="number"
                                name="sms"
                                id='sms'
                                value={userInput.sms}
                                onChange={handleChange}
                                required
                                placeholder="0"
                            />
                        </FormGroup>
                    </Col>
                    <Col sm="6" >
                        <FormGroup>
                            <Label for="email">Email</Label>
                            <Input type="number"
                                name="email"
                                id='email'
                                value={userInput.email}
                                onChange={handleChange}
                                required
                                placeholder="0"
                            />
                        </FormGroup>
                    </Col>
                    <Col sm="6" >
                        <FormGroup>
                            <Label for="push_notification">Push Notification</Label>
                            <Input type="number"
                                name="push_notification"
                                id='push_notification'
                                value={userInput.push_notification}
                                onChange={handleChange}
                                required
                                placeholder="0"
                            />
                        </FormGroup>
                    </Col>
                    <Col sm="6" >
                        <FormGroup>
                            <Label for="instagram">Instagram</Label>
                            <Input type="number"
                                name="instagram"
                                id='instagram'
                                value={userInput.instagram}
                                onChange={handleChange}
                                required
                                placeholder="0"
                            />
                        </FormGroup>
                    </Col>


                        <Col sm="12" className='text-center'>
                            {
                                pointRuleloading ? <Button.Ripple color='primary' className='mr-1' disabled style={{ marginTop: '25px' }}>
                                    <Spinner color='white' size='sm' />
                                    <span className='ml-50'>Loading...</span>
                                </Button.Ripple> : <Button.Ripple className='ml-2' color='primary' type="submit" style={{ marginTop: '25px' }}>
                                    <span >Submit</span>
                                </Button.Ripple>
                            }
                        </Col>
                    </Form>
                </CardBody>
            </Card>
        </Fragment>
    )
}

export default CreateQuota